<template>
    <div>
        <p-header>购物车</p-header>
        <div style="height: 0.9rem"></div>
        <div>
            <!--    收货时间制作开始-->
            <div class="time_box">
                <div class="time">
                    <div class="time_left">
                        <p>收货时间</p>
                    </div>
                    <div class="time_right">
                        <div class="time_right_txt">
                            <p>今天30分钟可送达（可预订）</p>
                        </div>
                        <div class="time_right_icon">
                            <i class="iconfont iconzuoyoujiantou1"></i>
                        </div>
                    </div>
                </div>
            </div>
            <!--    收获时间制作结束-->
            <!--    商品卡片一制作开始-->
            <div class="goodCar_list" v-if="goods">
                <div class="good-card_box" v-for="(item,index) in goods" :key="item.gid" >
                    <div class="good-card">
                        <div class="good-card_circle_box" @click="changestatus(item,index)">
                            <div class="good-card_circle" :style="{border:item.status?'none':''}">
                                <img :style="{display:item.status?'block':'none'}"  src="../../assets/cart/check4.png" alt="">
                            </div>
                        </div>
                        <div class="good-card_img_box" v-if="item.thumb">
                            <router-link :to="{name:'details',query:{id:item.gid}}">
                            <div class="good-card_img">
                                <img :src="imgpath+item.thumb" alt="">
                            </div>
                            </router-link>
                        </div>
                        <div class="good-card_txt_box">
                            <div class="good-card_title">
                                <p>{{item.gname}}</p>
                            </div>
                            <div class="good-card_price">
                                <span>￥</span>
                                <p>{{item.sale}}</p>
                            </div>
                            <div class="good-card_num">
                                <div class="good-card_num_del" @click="cutcartgood(item.gid,item.sale,$store.getters.goodsnum(item.gid))" >
                                    <img src="../../assets/classify/subtract.png" alt="">
                                </div>
                                <div class="good-card_num_txt" name="num1">{{$store.getters.goodsnum(item.gid)}}</div>
                                <div class="good-card_num_add" @click="addcartgood(item)">
                                    <img src="../../assets/classify/add.png" alt="">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="!goods">暂无数据</div>
            </div>
            <!--    商品卡片一制作结束-->
            <div class="kongs"></div>
        </div>
        <div class="my-cart">
            <!--    结算 制作开始-->
            <div class="shopTotal_box" v-if="$store.state.cart">
                <div class="shopTotal">
                    <div class="shopTotal_left">
                        <div class="shopTotal_circle" @click="changeall($store.state.allsta)" :class="{active1:changestatusall}">
                            <img src="../../assets/cart/check4.png" :class="{active:changestatusall}"  alt="">
                        </div>
                        <div class="shopTotal_txt">
                            <div>全选</div>
                            <div>合计:{{$store.state.cart.total}}<span>￥</span></div>
                            <div>{{$store.state.cart.price}}</div>
                        </div>
                    </div>
                    <div class="shopTotal_right">
                            <div class="shopTotal_btn" :class="{isshow:!changestatussome}" @click="ordersub">
                                去结算
                            </div>
                    </div>
                </div>
            </div>

            <!--    结算 制作结束-->
        </div>
        <z-footer :active="3"></z-footer>
    </div>
</template>

<script>
    import PHeader from '../../components/p-header/p-header';
    import ZFooter from '../../components/z-footer/z-footer';
    import {getgoods} from '../../api/goods';
    import {SUCCESS, IMGPATH} from "../../config/base";
    import {addgood,cutgood,changestatus} from "../../api/cart";
    import {mapState,mapGetters} from 'vuex'
    import {ordersub} from "../../api/orders";

    export default {
        name: "Mycart",
        data() {
            return {
                imgpath: IMGPATH,
                cartdata: null,
                goodsdata: null,
                goods: [],
            }
        },
        components: {
            ZFooter,
            PHeader
        },
        methods: {
            getCartGoods() {
                let isgoods=Boolean(this.$store.state.cart);
                console.log(isgoods);
                if (!isgoods){
                    return;
                }
                this.cartdata = this.$store.state.cart;
                this.goodsdata = this.cartdata.goods;
                getgoods('/api/cart').then(res => {
                    let data = res.data;
                    if (data.code === SUCCESS) {
                        this.goods=data.data.goods;
                    }
                });
            },
            addcartgood(item){
                let {sale,gid,gname,thumb,num,status}=item;
                addgood('/api/cart',{price:sale,gid,gname,thumb,num,status})
                    .then(res=>{
                        let data=res.data;
                        if (data.code===SUCCESS){
                            this.$store.commit('incgoods',{price:sale,gid,gname,thumb,num,status});
                            this.$store.commit('calcTotal');
                            this.$store.commit('calcPrice');
                        } else{
                            console.log(data.msg);
                        }
                    })
            },
            cutcartgood(gid,price,num){
                cutgood('/api/cutcart',{
                    price:price,
                    gid:gid
                }).then(res=>{
                    let data=res.data;
                    if (data.code===SUCCESS){
                        this.$store.commit('decgoods',gid);
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                        if (num===1){
                            this.getCartGoods();
                        }
                    } else{
                        console.log(data.msg);
                    }
                })
            },
            changestatus(item,index,num){
                let {gid,sale,status}=item;
                if (num===0){
                    return;
                }
                changestatus('/api/cutcart/'+gid,{
                    price:sale,
                    total:this.$store.getters.goodsnum(item.gid)
                })
                    .then(res=>{
                        if (res.data.code==SUCCESS) {
                            this.$store.commit('changestatus',gid);
                            this.goods[index].status=status?0:1;
                            this.$store.commit('calcTotal');
                            this.$store.commit('calcPrice');
                        }
                    })
            },
            ordersub(){
                ordersub('/api/orders').then(res=>{
                    if (res.data.code===SUCCESS){
                        console.log('订单创建成功');
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                        this.$router.push({name:'orders',query:{ordernum:res.data.ordersnum}});
                    }

                })
            },
            changeall(){
                // this.$store.commit('changestaall',sta);
                // changeall('/api/cart/'+sta)
                //     .then(res=>{
                //         if (res.data.code==SUCCESS) {
                //             this.$store.commit('calcTotal');
                //             this.$store.commit('calcPrice');
                //             for (let i=0;i<this.goods.length;i++){
                //                 this.goods[i].status=!sta*1;
                //                 console.log(!sta*1);
                //             }
                //     }
                // });
            }
        },
        beforeMount() {
            this.getCartGoods();
        },
        computed:{
            ...mapState({
                cart:'cart'
            }),
            ...mapGetters({
                changestatusall:'changestatusall',
                changestatussome:'changestatussome'
            })

        }
    }
</script>

<style scoped>
    @import url("../../style/car.css");
    .active{
        display: block;
    }
    .active1{
        border: none;
    }
    .isshow{
        background-color: #DEDEDE;
    }
</style>
